.owner-data {
  position: relative;
  color: @font-color;
  margin: 0;
  padding: 0;
  border: none;

  &::before {
    content: "";
    position: absolute;
    display: block;

    top: 15px;

    @media (min-width: @tablet-width) {
      top: 23px;
    }

    height: 2px;
    width: 100vw;

    z-index: -1;

    background-color: hsl(97, 53%, 47%);
  }
}

.owner-data__wrapper {
  position: relative;

  display: flex;
  flex-direction: column;


  row-gap: 20px;
  margin: 0 auto;
  padding: 0 0 24px;
  width: @mobile-width-only - @mobile-gap - @mobile-gap;

  @media (min-width: @tablet-width) {
    width: 92%;
    max-width: @desktop-width;
    margin: 0 auto;

    padding: 0 40px 65px;
  }

  @media (min-width: @desktop-width) {
    margin: 0 auto;
    padding: 0 40px 67px;

    max-width: @desktop-width - @desktop-gap - @desktop-gap;

    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 78px;
  }
}


.owner-data__title {
  align-items: center;

  margin: 0;
  margin-left: -20px;
  padding: 0 20px 15px;
  display: table;
  float: left;

  width: 225px;

  background-color: @white-color;

  font-family: @font;
  font-style: @font-style;
  font-weight: @font-weight;
  font-size: 24px;
  line-height: 26px;
  color: @font-color;

  &* {
    clear: both;
  }

  @media (min-width: @tablet-width) {
    padding: 0 40px 56px;
    margin-left: -40px;

    font-size: 36px;
    line-height: 36px;

    width: fit-content;
  }

  @media (min-width: @desktop-width) {
    grid-column-start: 1;
    grid-column-end: -1;
  }
}

.owner-data__email,
.owner-data__phone {
  display: flex;
  justify-content: space-between;

  font-family: @font;
  font-style: @font-style;
  font-weight: @font-weight;
  font-size: 16px;
  line-height: 16px;
  transform-style: uppercase;

  align-items: center;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 30px;
  }
}

.owner-data__email {
  position: relative;

  &::after {
    position: absolute;

    content: url("../img/icon-email.svg");
    display: block;
    margin: 0;
    padding: 0;
    top: 45%;
    transform: translateY(-50%);

    right: 20px;
  }
}

.owner-data__email-input:focus + .owner-data__email::after {
  position: absolute;

  content: url("../img/icon-email-focus.svg");
  display: block;
  margin: 0;
  padding: 0;
  top: 45%;
  transform: translateY(-50%);

  right: 20px;
}

.owner-data__phone {
  position: relative;

  &::after {
    position: absolute;

    content: url("../img/icon-phone.svg");
    display: block;
    margin: 0;
    padding: 0;
    top: 50%;
    transform: translateY(-50%);

    right: 20px;
  }
}

.owner-data__phone-input:focus + .owner-data__phone::after {
  position: absolute;

  content: url("../img/icon-phone-focus.svg");
  display: block;
  margin: 0;
  padding: 0;
  top: 50%;
  transform: translateY(-50%);

  right: 20px;
}

.owner-data__email-label,
.owner-data__phone-label {
  display: block;

  padding-right: 13px;
  flex-basis: auto;
}

.owner-data__email-input,
.owner-data__phone-input {
  padding: 15px 10px 15px 15px;
  flex-grow: 1;
  flex-basis: 70%;

  font-family: @font;
  font-style: @font-style;
  font-weight: @font-weight;
  font-size: 16px;
  line-height: 18px;
  transform-style: uppercase;

  align-items: center;

  @media (min-width: @tablet-width) {
    padding: 12px 60px 12px 21px;

    font-size: 20px;
    line-height: 30px;
  }

  @media (min-width: @desktop-width) {
    padding: 8px 60px 8px 17px;
  }
}
